Office.onReady(info => {
	if (info.host === Office.HostType.Outlook) {
		showDetailView(Office.context.mailbox.item);
	}
});

function showDetailView(mailItem) {
	console.log(mailItem);
	Office.context.mailbox.item.body.getAsync(Office.CoercionType.Html, function(body) {
		//console.log(body.value);
		AppConnector.request({
			module: 'MailIntegration',
			view: 'Detail',
			source: 'outlook'
			// mail_from: mailItem.from.emailAddress,
			// mail_subject: mailItem.subject,
			// mail_normalizedSubject: mailItem.normalizedSubject,
			// mail_sender: mailItem.sender.emailAddress,
			// mail_body: body.value
		})
			.done(function(responseData) {
				console.info(responseData);
				$('#page').html(responseData);
			})
			.fail(function(error) {
				console.error(error);
			});
	});
}
